import React, { useState, useEffect } from 'react';
import axios from '../axios';
import './FoodRec.css';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';

const mapContainerStyle = {
  width: '100%',
  height: '400px',
};

const API_KEY = 'AIzaSyBotoIOq5LKbKo-FoB7cWC9DKSj0EAScPU';

function FoodRec() {
  const [hasFavorite, setHasFavorite] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [recommendations, setRecommendations] = useState([]);
  const [criteria, setCriteria] = useState({ type: '', price: '' });
  const [location, setLocation] = useState(null);

  useEffect(() => {
    if (searchResults.length > 0) {
      showMap(searchResults[0].address);
    }
  }, [searchResults]);

  const getGeocode = async (address) => {
    try {
      const response = await axios.get(`https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(address)}&key=${API_KEY}`);
      const location = response.data.results[0]?.geometry.location;
      return location;
    } catch (error) {
      console.error('Error getting location:', error);
      return null;
    }
  };

  const handleSearch = async () => {
    console.log('식당 검색 요청:', searchTerm);
    try {
      const res = await axios.post('/api/restaurants/restaurant-info', { title: searchTerm });
      console.log('검색 결과:', res.data);
      setSearchResults([res.data]);
      const loc = await getGeocode(res.data.address);
      setLocation(loc);
    } catch (err) {
      console.error('Error searching for restaurant:', err);
      setSearchResults([]);
    }
  };

  const handleRecommend = async () => {
    console.log('식당 추천 요청:', criteria);
    try {
      const res = await axios.post('/api/restaurants/recommend', { criteria });
      console.log('추천 결과:', res.data);
      setRecommendations(await Promise.all(res.data.map(async (rec) => {
        const loc = await getGeocode(rec.address);
        return { ...rec, location: loc };
      })));
    } catch (err) {
      console.error('Error getting recommendations:', err);
      setRecommendations([]);
    }
  };

  const addFavorite = (restaurant) => {
    const token = localStorage.getItem('token'); // Assuming token is stored in localStorage
    console.log('즐겨찾기 추가 요청:', restaurant);
    axios.post('/api/restaurants/favorites', restaurant, {
      headers: {
        'Authorization': `Bearer ${token}`
      }
    })
      .then(res => {
        console.log('즐겨찾기 추가 성공:', res.data);
        toast.success('즐겨찾기 추가되었습니다!', {
          position: "top-right"
        });
      })
      .catch(err => {
        console.error('Error adding favorite:', err);
        if (err.response && err.response.data.message) {
          toast.error(err.response.data.message, {
            position: "top-right"
          });
        } else {
          toast.error('즐겨찾기 추가 실패!', {
            position: "top-right"
          });
        }
      });
  };

  const handleHasFavoriteChange = (value) => {
    setHasFavorite(value);
    setSearchTerm('');
    setSearchResults([]);
    setRecommendations([]);
  };

  const showMap = async (address) => {
    const location = await getGeocode(address);
    setLocation(location);
  };

  return (
    <div className="foodrec-container">
      <ToastContainer />
      <h2>맛집 추천 서비스</h2>
      <div>
        <label>가고 싶은 식당이 있나요?</label>
        <div>
          <button onClick={() => handleHasFavoriteChange(true)}>네</button>
          <button onClick={() => handleHasFavoriteChange(false)}>아니요</button>
        </div>
      </div>

      {hasFavorite === true && (
        <div className="favorite-search">
          <input
            type="text"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            placeholder="식당 이름을 입력하세요"
          />
          <button onClick={handleSearch}>검색</button>
          <div className="search-results">
            {searchResults.length > 0 ? (
              searchResults.map((result, index) => (
                <div key={index} className="favorite-item">
                  <p>이름: {result.name}</p>
                  <p>음식 종류: {result.type}</p>
                  <p>가격: {result.price}</p>
                  <p>네이버 별점: {result.rating}</p>
                  <p>위치: {result.address}</p>
                  {result.reviewCount && <p>블로그 리뷰수: {result.reviewCount}</p>}
                  <button onClick={() => addFavorite(result)}>즐겨찾기 추가</button>
                </div>
              ))
            ) : (
              <p>검색 결과가 없습니다.</p>
            )}
          </div>
          {location && (
            <LoadScript googleMapsApiKey={API_KEY}>
              <GoogleMap
                mapContainerStyle={mapContainerStyle}
                center={location}
                zoom={15}
              >
                <Marker position={location} />
              </GoogleMap>
            </LoadScript>
          )}
        </div>
      )}

      {hasFavorite === false && (
        <div className="no-favorite">
          <div className="recommendation-criteria">
            <input
              type="text"
              value={criteria.type}
              onChange={(e) => setCriteria({ ...criteria, type: e.target.value })}
              placeholder="음식 종류 입력"
            />
            <input
              type="number"
              value={criteria.price}
              onChange={(e) => setCriteria({ ...criteria, price: e.target.value })}
              placeholder="가격대 입력"
            />
          </div>
          <button onClick={handleRecommend}>추천 받기</button>
          <div className="recommendations">
            {recommendations.length > 0 ? (
              recommendations.map((rec, index) => (
                <div key={index} className="recommendation-item">
                  <p>이름: {rec.name}</p>
                  <p>음식 종류: {rec.type}</p>
                  <p>가격: {rec.price}</p>
                  <p>네이버 별점: {rec.rating}</p>
                  <p>위치: {rec.address}</p>
                  {rec.reviewCount && <p>블로그 리뷰수: {rec.reviewCount}</p>}
                  <button onClick={() => addFavorite(rec)}>즐겨찾기 추가</button>
                  {rec.location && (
                    <LoadScript googleMapsApiKey={API_KEY}>
                      <GoogleMap
                        mapContainerStyle={mapContainerStyle}
                        center={rec.location}
                        zoom={15}
                      >
                        <Marker position={rec.location} />
                      </GoogleMap>
                    </LoadScript>
                  )}
                </div>
              ))
            ) : (
              <p>추천 결과가 없습니다.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default FoodRec;