import React, { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';
import { UserContext } from '../contexts/UserContext';
import './Monthly.css';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

const Monthly = () => {
  const { user } = useContext(UserContext);
  const [currentMonthData, setCurrentMonthData] = useState([]);
  const [previousMonthData, setPreviousMonthData] = useState([]);
  const [currentYear, setCurrentYear] = useState(new Date().getFullYear());
  const [currentMonth, setCurrentMonth] = useState(new Date().getMonth() + 1);
  const userId = user ? user._id : null;
  const today = new Date().getDate();
  const [fixedExpenses, setFixedExpenses] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      if (!userId) return;
      try {
        const currentMonthRes = await axios.get(`http://localhost:5000/api/expenses/month/${userId}/${currentYear}/${currentMonth}`);
        const previousMonth = currentMonth === 1 ? 12 : currentMonth - 1;
        const previousYear = currentMonth === 1 ? currentYear - 1 : currentYear;
        const previousMonthRes = await axios.get(`http://localhost:5000/api/expenses/month/${userId}/${previousYear}/${previousMonth}`);
        const fixedRes = await axios.get(`http://localhost:5000/api/fixed-expenses/${userId}/${currentYear}/${currentMonth}`);

        setFixedExpenses(fixedRes.data);

        const calculateCumulative = (data, fixedData) => {
          let cumulative = [];
          let sum = 0;
          for (let i = 1; i <= 31; i++) {
            sum += data[i] ? Object.values(data[i]).reduce((acc, curr) => acc + Number(curr), 0) : 0;
            if (i === 1) {
              fixedData.forEach(expense => {
                sum += Object.values(expense.amounts).reduce((acc, curr) => acc + Number(curr), 0);
              });
            }
            cumulative.push(sum);
          }
          return cumulative;
        };

        const currentCumulative = calculateCumulative(currentMonthRes.data, fixedRes.data).slice(0, today);
        const previousCumulative = calculateCumulative(previousMonthRes.data, []);

        console.log('이번 달 누적 지출 데이터:', currentCumulative);
        console.log('지난 달 누적 지출 데이터:', previousCumulative);

        setCurrentMonthData(currentCumulative);
        setPreviousMonthData(previousCumulative);
      } catch (err) {
        console.error('지출 정보 불러오기 실패:', err);
      }
    };
    fetchData();
  }, [userId, currentYear, currentMonth, today]);

  const labels = Array.from({ length: Math.max(today, previousMonthData.length) }, (_, i) => `${i + 1}`);

  const data = {
    labels,
    datasets: [
      {
        label: `${currentMonth}월`,
        data: currentMonthData,
        borderColor: 'blue',
        backgroundColor: 'rgba(0, 0, 255, 0.2)',
        fill: true,
      },
      {
        label: `${currentMonth === 1 ? 12 : currentMonth - 1}월`,
        data: previousMonthData,
        borderColor: 'gray',
        backgroundColor: 'rgba(128, 128, 128, 0.2)',
        fill: true,
      },
    ],
  };

  return (
    <div className="monthly-container">
      <h2>월별 통계</h2>
      <Line data={data} />
    </div>
  );
};

export default Monthly;