import React from 'react';
import { Route, Routes, useNavigate, useLocation } from 'react-router-dom';
import Header from './components/Header';
import Calendar from './components/Calendar';
import Monthly from './components/Monthly';
import Statistics from './components/Statistics';
import MyPage from './components/MyPage';
import BudgetInput from './components/BudgetInput';
import FixedExpenses from './components/FixedExpenses';
import SpendingCategory from './components/SpendingCategory';
import Notices from './components/Notices';
import Help from './components/Help';
import Login from './components/Login';
import Register from './components/Register';
import FoodRec from './components/FoodRec';
import Favorites from './components/Favorites'; // Import Favorites
import { UserProvider } from './contexts/UserContext';
import './styles/App.css';

function App() {
  const navigate = useNavigate();
  const location = useLocation();

  const showNavButtons = ['/','/monthly','/statistics'].includes(location.pathname);

  return (
    <UserProvider>
      <div className="App">
        <Header />
        {showNavButtons && (
          <div className="content-nav">
            <button onClick={() => navigate('/')}>달력</button>
            <button onClick={() => navigate('/monthly')}>월별</button>
            <button onClick={() => navigate('/statistics')}>통계</button>
          </div>
        )}
        <main>
          <Routes>
            <Route path="/" element={<Calendar />} />
            <Route path="/monthly" element={<Monthly />} />
            <Route path="/statistics" element={<Statistics />} />
            <Route path="/mypage" element={<MyPage />} />
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            <Route path="/budget-input" element={<BudgetInput />} />
            <Route path="/fixed-expenses" element={<FixedExpenses />} />
            <Route path="/spending-category" element={<SpendingCategory />} />
            <Route path="/notices" element={<Notices />} />
            <Route path="/help" element={<Help />} />
            <Route path="/restaurants" element={<FoodRec />} />
            <Route path="/favorites" element={<Favorites />} /> {/* Add Favorites route */}
          </Routes>
        </main>
      </div>
    </UserProvider>
  );
}

export default App;